import {useHistory, useParams} from "react-router-dom";
import {useContext, useEffect, useState} from "react";
import {connect} from "react-redux";
import TabComponent from "./useablecomponent/TabComponent";
import styled from "styled-components";
import "./css/detail.scss";
import {StockContext} from "../App";

function ItemsDetail(props) {
    let history = useHistory();
    let {id} = useParams();
    let item = props.itemList;
    const [out, setOut] = useState(true);
    const [input, setInput] = useState('');

        let sawItems = localStorage.getItem('sawItems');
        let sawItemData = JSON.parse(sawItems);
        let v = [];
        if(sawItemData != null ){
            v = sawItemData.find( e =>e.id===id);
        }
        console.log("v는 뭐지?")
                console.log(v)
        if(localStorage.getItem('sawItems') === null){//sawItems === null
                localStorage.setItem('sawItems',JSON.stringify([{id}]));
                sawItems = localStorage.getItem('sawItems');
                sawItemData = JSON.parse(sawItems);
//                v = sawItemData.find(e=>e.id===id);
            }else if(v.id!==id){//sawItemData.filter(e=>e.id==={id})!==null
                sawItemData.push({id}); console.log("sawItemData"+sawItemData);
                localStorage.setItem('sawItems',JSON.stringify(sawItemData))
                console.log("2번")
            }

/*    useEffect(() => {
        let timesUp = setTimeout(() => {
            setOut(false);
        }, 3000)
        return () => {
            clearTimeout(timesUp);
            console.log('Component의 Lifecycle이 끝나고나서 실행된다.')
        }
    }, []);*/

    let stock = useContext(StockContext);
    let Title = styled.h1`
      color: ${props => props.fontColor};
    `

    return (
        <div className="container">
            <br/><Title fontColor={'black'}>DETAIL</Title>
            {/*            <input onChange={(e) => {
                setInput(e.target.value)
            }}/>
            <p>{input}</p>*/}
            <div className="row">
                <div className="col-md-6 mt-5">
                    <img src={item[id].src} width="100%"/>
                </div>
                <div className="col-md-6 mt-0">
                    <h4 className="pt-5">{item[id].title}</h4>
                    <p>상품설명 : {item[id].content}</p>
                    <p>가격 : {item[id].price}</p>
                    <button className="btn btn-danger" onClick={() => {
                        props.dispatch({
                            type: '장바구니추가',
                            payload: {
                                id: item[id].id,
                                src: item[id].src,
                                title: item[id].title,
                                price: item[id].price,
                                content: item[id].content,
                                quan : 1
                            }
                        })
                    }}>
                        장바구니 추가
                    </button>
                    <br/><br/>
                    <button className="btn btn-danger" onClick={() => {
                        history.goBack()
                    }}>뒤로가기
                    </button>
                    <br/><br/><br/>
                    <p>
                        남은재고 : {item[id].stock}
                    </p>
                    {
                        out ?
                            <div className="my-alert2">
                                <p>품절 임박상품</p>
                            </div>
                            : false
                    }
                    <TabComponent/>
                </div>
            </div>
            <p> dev; 'match' parameter : {id}번째 객체 id의 상품 </p>
            <br/>
        </div>
    );
}

function toProp(state) {
    return {
        cart: state.reducer
    }
}

export default connect(toProp)(ItemsDetail);